<template><section>
<h2>Carousel 走马灯</h2>
<p>循环播放图片，文字内容等</p>
<h3>基础用法</h3>
<coding title="基础用法" note="走马灯默认1000ms切换一次，指示器默认通过点击触发" :code="baseCode">
  <RuCarousel>
    <RuCarouselItem>
      <div class="item">1</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">2</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">3</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">4</div>
    </RuCarouselItem>
  </RuCarousel>
</coding>
<h3>触发指示器</h3>
<p>指示器可以修改为Hover触发</p>
<coding title="Hover触发" note="指示器可以修改为Hover触发" :code="hoverCode">
  <RuCarousel trigger="hover">
    <RuCarouselItem>
      <div class="item">1</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">2</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">3</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">4</div>
    </RuCarouselItem>
  </RuCarousel>
</coding>
<h3>控制箭头显示</h3>
<p>可以设置箭头显示的时机</p>
<coding title="控制箭头显示隐藏" note="通过showArrow字段可以设置箭头的显示时机" :code="arrowCode">
  <RuCarousel showArrow="always">
    <RuCarouselItem>
      <div class="item">1</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">2</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">3</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">4</div>
    </RuCarouselItem>
  </RuCarousel>
  <RuCarousel showArrow="hover">
    <RuCarouselItem>
      <div class="item">1</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">2</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">3</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">4</div>
    </RuCarouselItem>
  </RuCarousel>
</coding>
<h3>外置指示器</h3>
<p>指示器可以放在组件外部显示</p>
<coding title="指示器外置" note="设置dotsOutside可以将指示器放在组件外部显示" :code="dotCode">
  <RuCarousel dotsOutside="">
    <RuCarouselItem>
      <div class="item">1</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">2</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">3</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">4</div>
    </RuCarouselItem>
  </RuCarousel>
</coding>
<h3>Attributes</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>dotsOutside</td>
<td>指示器是否外置</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>height</td>
<td>走马灯的高度</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>duration</td>
<td>时间间隔（为0时不自动切换）</td>
<td>Number</td>
<td>-</td>
<td>1000</td>
</tr>
<tr>
<td>showArrow</td>
<td>箭头的显示时机</td>
<td>String</td>
<td>always/never/hover</td>
<td>hover</td>
</tr>
<tr>
<td>trigger</td>
<td>指示器的触发时机</td>
<td>String</td>
<td>hover/click</td>
<td>click</td>
</tr>
<tr>
<td>index</td>
<td>初始内容索引</td>
<td>Number</td>
<td>-</td>
<td>0</td>
</tr>
</tbody>
</table>
</section></template>
<style lang="scss" scoped>
  .ru-carousel-item {
    .item {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      font-size: 20px;
      color: #fff;
    }
    &:nth-child(odd) {
      background-color: #4b6b9c;
    }
    &:nth-child(even) {
      background-color: #d3dce6;
    }
  }
</style>
<script>
const baseCode = `
  <RuCarousel>
    <RuCarouselItem>
      <div class="item">1</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">2</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">3</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">4</div>
    </RuCarouselItem>
  </RuCarousel>`

  const hoverCode = `
  <RuCarousel trigger="hover">
    <RuCarouselItem>
      <div class="item">1</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">2</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">3</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">4</div>
    </RuCarouselItem>
  </RuCarousel>`

  const arrowCode = `
  <RuCarousel showArrow="always">
    <RuCarouselItem>
      <div class="item">1</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">2</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">3</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">4</div>
    </RuCarouselItem>
  </RuCarousel>
  <RuCarousel showArrow="hover">
    <RuCarouselItem>
      <div class="item">1</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">2</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">3</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">4</div>
    </RuCarouselItem>
  </RuCarousel>
  `

  const dotCode = `
  <RuCarousel dotsOutside">
    <RuCarouselItem>
      <div class="item">1</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">2</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">3</div>
    </RuCarouselItem>
    <RuCarouselItem>
      <div class="item">4</div>
    </RuCarouselItem>
  </RuCarousel>
  `
export default {
  data() {
    return {
      baseCode: baseCode,
      hoverCode: hoverCode,
      arrowCode: arrowCode,
      dotCode: dotCode
    }
  }
}
</script>


